






































import {Component, Vue} from 'vue-property-decorator';
import {Divider, Row, Col, Button} from 'vant';
import 'vant/lib/divider/style';
import 'vant/lib/row/style';
import 'vant/lib/col/style';
import 'vant/lib/button/style';
import {AuthBusinessStatus, AuthBusinessType, Business} from "../../model/business_model";
import {ToastUtil} from "../../util/toast";

Vue.use(Divider).use(Row).use(Col).use(Button);

@Component
export default class Process extends Vue {
    private businessModel = new Business();             //
    private authTypeText = "未知";                       // 操作类型
    private authStatusText = "失败";                     // 授权状态
    private txAddress = "";                              // 地址
    private txName = "";                                 // 姓名

    public created(): void {
        this.$store.state.title = "信息上链进度";
    }

    public mounted() {
        this.businessModel = this.$store.state.nowBusiness;
        if (this.businessModel.authType === AuthBusinessType.Auth) {
            this.authTypeText = "增加业务员";
        } else if (this.businessModel.authType === AuthBusinessType.Cancel) {
            this.authTypeText = "删除业务员";
        } else {
            ToastUtil.show("授权类型未知，请重新查证");
        }
        if (this.businessModel.authState === AuthBusinessStatus.SUCCESS) {
            this.authStatusText = "授权完成";
        } else {
            this.authStatusText = "授权失败";
        }
        this.txAddress = this.businessModel.address;
        this.txName = this.businessModel.name;
    }

    public clickToHome(): void {
        this.$router.replace({name: 'cert_search'});
    }
}
